.button {
  font: inherit;
  border: 1px solid rgb(242, 109, 109);
  background: rgb(242, 109, 109);
  color: white;
  padding: 0.25rem 1rem;
  cursor: pointer;
}

.button:hover,
.button:active {
  background: rgb(250, 138, 138);
  border-color: rgb(250, 138, 138);
}

.button:focus {
  outline: none;
}
