.toDoStyle {
  background-color: #f5cc28;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.toDoStyle button {
  font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #f5da6d;
  background-color: #f5da6d;
  color: white;
  border-radius: 12px;
  margin-right: 1rem;
}

.toDoStyle button:hover,
.toDoStyle button:active {
  background-color: #f8ecba;
  border-color: #f8ecba;
}

.toDoStyle button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.toDoStyle button.alternative:hover,
.toDoStyle button.alternative:active {
  background-color: #ddb3f8;
}
