.toDoForm {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.toDoFormControl label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.toDoFormControl input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.toDoFormControlActions {
  text-align: center;
  size: 1px;
}
